/* AnalyticsDashboard.css */

/* Enhanced styles for AnalyticsDashboard */
.analytics-container {
    max-width: 1000px;
    margin: 2rem auto;
    padding: 1.5rem 2rem;
    background: linear-gradient(135deg, #f9fafb, #e5e7eb);
    border-radius: 16px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

.analytics-container:hover {
    transform: scale(1.02);
}

.analytics-title {
    font-size: 2.5rem;
    font-weight: 800;
    color: #1f2937;
    margin-bottom: 2.5rem;
    text-align: center;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.chart-card {
    background-color: #ffffff;
    border-radius: 12px;
    padding: 1.5rem;
    margin-bottom: 2rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease;
}

.chart-card:hover {
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.chart-card h3 {
    font-size: 1.5rem;
    font-weight: 700;
    color: #374151;
    margin-bottom: 1rem;
}

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-size: 1.5rem;
    color: #6b7280;
}

.error {
    text-align: center;
    color: #ef4444;
    font-size: 1.25rem;
    margin-top: 2rem;
}
